form input {
  border-radius: 6px;
  height: 40px;
  border: none;
  background-color:#fff;
  border : 1.5px solid #c7cbd0 ;
  width : 320px;
 
} 
form button{
  width : 320px;
  height : 40px;
  border-radius: 6px;
  background-color: dodgerblue;
  color: white;
  border : none;
  font-weight: 600;
}
form p {
  color: dodgerblue;
  font-weight:500;
  font-family: inherit;
  font-size:14px;

}
.acb{
  height : 380px;
  width : 400px;
  background-color: whitesmoke;
  text-align: center;
  margin: 0 auto;
  margin-top: 170px;
  border-radius: 6px;
}
form{
  height : 400px;
  width : 400px;
  background-color: #fff;
  text-align: center;
  margin: 0 auto;
  margin-top: 170px;
  border-radius: 6px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.6);
}
body{
  /* background-color: rgb(100, 168, 237); */
  background-color: rgb(209, 214, 220);
}
form button:hover{
  cursor: pointer;
  box-shadow: 3px 3px rgb(12, 12, 12);
}
form p:hover {
cursor : pointer;
text-decoration: underline;
}
form input:hover {
  border : 1.5px solid dodgerblue;
  box-shadow: 1px 1px 1px dodgerblue;
}
